<template lang="pug">
  #app
    router-view
    video#video(autoplay,height="300px")
</template>

<script>
import Cookie from 'js-cookie'
import axios from 'axios'
import {mapActions} from 'vuex'

export default {
  mounted () {
    let cons = window.constraints = {
      audio: false,
      video: true
    }
    let video = document.getElementById('video')
    navigator.mediaDevices.getUserMedia(cons)
      .then((stream) => {
        var videoTracks = stream.getVideoTracks();
        console.log('Got stream with constraints:', cons);
        console.log('Using video device: ' + videoTracks[0].label);
        stream.onended = function() {
          console.log('Stream ended');
        };
        window.stream = stream; // make variable available to console
        video.srcObject = stream;
      })
      .catch((err) => {
        console.log(err);
      })
  },  
  methods: {
    ...mapActions([
      'setUserInfo'
    ])
  },
  created () {
    let tokenID = Cookie.get('tokenID');
    if (tokenID) {
      let data = {
        tokenID
      }
      axios.post('http://dfy/bin/loginByToken', data).then(res => {
        let cb = res.data
        if (cb.state) {
          Cookie.set('tokenID', cb.data.tokenID, {expires: .5})
          this.setUserInfo(cb.data);
          // this.$router.push('/member/info');
          console.log(this.$store.state);
        } else {
          // this.$message('用户名或密码')
        }
        this.$message(cb.message)
      })
    }
  }
}
</script>
<style lang="scss">
  #video {
    position: fixed;
    bottom: 0;
    resize: both;}
</style>



